.finish {
  padding: 16px;
  background: var(--finish-bg);
  min-height: 100vh;
  max-width: 1440px;
  margin: 0 auto;
}
.finish-img {
  display: block;
  width: 198px;
  height: 157px;
  margin: 0 auto;
  margin-top: 82px;
  margin-bottom: 50px;
}
.finish-title {
  font-size: 18px;
  font-weight: 600;
  text-align: center;
  margin-bottom: 7px;
  color: var(--finish-title);
  opacity: 0.5;
}
.finish-score {
  font-size: 32px;
  font-weight: 600;
  text-align: center;
  color: var(--start-title);
  margin-bottom: 100px;
}
.finish-btn {
  width: 288px;
  height: 48px;
  background-color: var(--finish-btn);
  border-radius: 8px;
  font-weight: 500;
  color: white;
  font-size: 14px;
  transition-duration: 0.25s;
  margin: 0 auto;
  padding: 0;
}
.finish-btn:hover {
  background-color: var(--finish-btn-hover);
}
.finish-btn:focus {
  background-color: var(--finish-btn-active);
}
.finish-link {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
@media screen and (min-width: 1439px) {
  .finish {
    display: flex;
    justify-content: flex-start;
  }
  .finish-img {
    display: block;
    min-width: 452px;
    height: 360px;
    margin: 142px 122px 100px 154px;
  }
  .finish-title {
    font-size: 32px;
    text-align: left;
    margin-bottom: 63px;
    margin-bottom: 0;
  }
  .finish-score {
    font-size: 56px;
    margin-bottom: 56px;
  }
  .finish-content__lg {
    margin-top: 240px;
  }
  .finish-btn {
    font-size: 20px;
    margin: 0;
    font-size: 20px;
    width: 296px;
    height: 64px;
  }
}
